import IReportRow from "./report-row";
import storageUtil from "./storageUtil";

const URL_PREFIX_LIST = [
    "bond-finder-lab.github.io/backend",
    "storage.yandexcloud.net/bond-finder",
    "global.s3.cloud.ru/bond-finder"
] as const;

const URL_PREFIX_COUNT = URL_PREFIX_LIST.length;
const URL_PREFIX_OFFSET = Date.now() % URL_PREFIX_COUNT;

const CACHE_KEY_REPORT = "report-cache-v1";
const CACHE_KEY_DATE = "date";

const LAST_DATE = storageUtil.load(CACHE_KEY_DATE) || "";

interface IReport {
    readonly date: string;
    readonly rows: ReadonlyArray<IReportRow>;
    readonly error?: boolean;
}

type ReportReadyCallback = (report: IReport) => void;

function convert(source: any): IReport {
    const cols = source.cols as string[];
    const rows = source.rows as any[];

    const indexOf: { [jsonKey: string]: number } = {};
    for (const name of cols) {
        indexOf[name] = cols.indexOf(name);
    }

    return {
        date: String(source.date),
        rows: rows.map((r) => {
            const isin = String(r[indexOf.isin]);
            const name = String(r[indexOf.name]);
            const sector = Number(r[indexOf.sector]);
            const listLevel = Number(r[indexOf.list_level]);
            const totalTradesPerc = Number(r[indexOf.total_trades_perc]);
            const maturityDate = String(r[indexOf.maturity_date]);
            const minDaysBetweenAmort = Number(r[indexOf.min_days_between_amort]);
            const couponMonths = r[indexOf.coupon_months] as readonly number[];
            const couponValues = r[indexOf.coupon_values] as readonly number[];
            const closeYield = Number(r[indexOf.close_yield]);
            const hasOffer = Boolean(r[indexOf.has_offer]);
            const inABI = Boolean(r[indexOf.in_abi]);
            const inCBHY = Boolean(r[indexOf.in_cbhy]);
            const isHighRisk = Boolean(r[indexOf.high_risk]);
            const isQualOnly = Boolean(r[indexOf.qual]);
            const rub = !("rub" in indexOf) || Boolean(r[indexOf.rub]);
            const rating = Number(r[indexOf.rating]);
            const waprice = Number(r[indexOf.waprice]);
            const amortRangeDays = Number(r[indexOf.amort_range_days]);
            const pending = Boolean(r[indexOf.pending]);

            const isOFZPK = isOFZ("ПК");
            const isOFZIN = isOFZ("ИН");

            function isOFZ(type: string) {
                return sector === 1
                    && name.substring(0, 4) === "ОФЗ-"
                    && name.substring(4, 6) === type;
            }

            const couponValuesLen = couponValues.length;

            const couponNextValue = couponValues[0];
            const couponRemainingAvgValue = couponValues[couponValuesLen - 1];

            function calcCurrentYield() {
                const coupon = couponRemainingAvgValue || couponNextValue;

                if (coupon && waprice) {
                    return 100 * coupon / waprice;
                }

                return 0;
            }

            function calcCouponType() {
                if (hasOffer) {
                    return 3;
                }

                if (couponRemainingAvgValue === null || isOFZPK) {
                    return 2;
                }

                if (couponValuesLen > 1) {
                    return 1;
                }

                return 0;
            }

            return {
                isin,
                name,
                sector,
                listLevel,
                totalTradesPerc,
                maturityDate,
                minDaysBetweenAmort,
                couponMonths,
                couponValues,
                closeYield,
                hasOffer,
                inABI,
                inCBHY,
                isHighRisk,
                isQualOnly,
                rub,
                rating,
                waprice,
                amortRangeDays,
                pending,

                isOFZPK,
                isOFZIN,
                currentYield: calcCurrentYield(),
                couponType: calcCouponType(),
                hasFutureAmort: minDaysBetweenAmort > 0, // amortRangeDays is auxiliary

                fits: false,
                top: false,
                infoUrl: null
            };
        })
    };
}

function convertFromTextSafe(text: string): IReport {
    try {
        return convert(JSON.parse(text));
    } catch {
        return {
            error: true,
            date: null,
            rows: [ ]
        };
    }
}

function loadCore(urlIndex: number, callback: ReportReadyCallback) {
    const url = "https://"
        + URL_PREFIX_LIST[(URL_PREFIX_OFFSET + urlIndex) % URL_PREFIX_COUNT]
        + "/report-v1.json?_=" + Date.now();

    const isLastUrl = urlIndex === URL_PREFIX_COUNT - 1;

    const xhr = new XMLHttpRequest();
    xhr.open("GET", url, true);
    xhr.timeout = (isLastUrl ? 10 : 3) * 1000;

    function handleResponseText(responseText: string) {
        let report = convertFromTextSafe(responseText);
        if (report.error || report.date < LAST_DATE) {
            if (!isLastUrl) {
                loadCore(1 + urlIndex, callback);
                return;
            }
            report = convertFromTextSafe(storageUtil.load(CACHE_KEY_REPORT));
        } else {
            storageUtil.save(CACHE_KEY_REPORT, responseText);
            storageUtil.save(CACHE_KEY_DATE, report.date);
        }
        callback(report);
    }

    xhr.onreadystatechange = function() {
        if (this.readyState === 4) {
            const { status, responseText } = this;
            handleResponseText(status === 200 ? responseText : null);
        }
    };

    xhr.send();
}

export default (callback: ReportReadyCallback) => loadCore(0, callback);
